<template>
  <transition-group name="list-complete" tag="ul" class="todos">
    <li v-for="(item, index) in todos" :key="index" :class="['todoItem','list-complete-item']">
      <input type="checkbox" :checked='item.completed' @click='markComplete(index)' class="checkbox">
      <span :class="{complete:item.completed}" >{{item.title}}</span>
      <button @click='deleteTodo(index)' class="button1">x</button>
    </li>
  </transition-group>
</template>
<script setup>
import { defineProps,defineEmits} from "vue";
  const props = defineProps({
    todos:Array
  })
  const emits = defineEmits(['delete-todo','mark-complete'])
  console.log(props.todos);
  const deleteTodo = (index) =>{
    emits('delete-todo',index)
  }
  const markComplete = (index) =>{
    emits('mark-complete',index)
  }
</script>
<style scoped>
.todos{
  width: 600px;
  height: 600px;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
span{
  font-size: 24px;
  font-family: "Microsoft soft";
  font-weight: 700;
  flex: 1;
}
.todoItem{
  display: flex;
  width: 80%;
  height: 44px;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 10px;
  margin-bottom: 5px;
  justify-content: space-between;
  align-items: center;
  padding-left: 14px;
}

.checkbox{
  width: 30px;
  height:100%;
  border-radius: 50%;
  border: none;
  background: #4abf8a;
}
.button1{
  width: 44px;
  border: 0px;
  background: white;
  font-size: 24px;
}
.complete{
  color: #ccc;
  text-decoration:line-through; 
}

.list-complete-item {
  transition: all 0.8s ease;
}
.list-complete-enter-from,
.list-complete-leave-to {
  opacity: 0;
}
.list-complete-move{
  transition: all 1s;
}
</style>