<template>
  <div class="Addtodo">
    <input type="text" placeholder="add todo.." v-model="title" />
    <button @click="addtodo">addTodo</button>
  </div>
</template>
<script setup>
import { ref, defineEmits } from "vue";
const title = ref('')
const emits = defineEmits(['add-todo'])
const addtodo = () => {
  if(title.value){
    emits('add-todo', title.value)
  }
  title.value = ''
}
</script>
<style scoped>
.Addtodo {
  width: 100vw;
  height: 55px;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
input{
  outline-style: none ; 
  border: 2px solid #ccc; 
  border-radius: 10px 0px 0px 10px;
  padding: 14px 14px;
  width: 50%;
  font-size: 24px;
  font-family: "Microsoft soft";
  font-weight: 700;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
input:focus{
  border: 2px solid #4abf8a; 
}
button{
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border: 0px;
  border-radius: 0em 10px 10px 0em;
  color: #eee;
  line-height: 40px;
  padding: 0 14px;
  background: #4abf8a;
  font-size: 24px;
  font-family: "Microsoft soft";
  font-weight: 700;
}
button:active{
  animation-name: scaleButton;
  animation-duration: 2s;
} 
@keyframes scaleButton {
  0% {
    font-size: 20px;
  }
  50% {
    font-size: 28px;
  }
  100% {
    font-size: 24px;
  }
}
</style>